<template>
  <v-card id="paq">
    <v-toolbar height="30px" elevation="0" color="primary">
      <v-spacer />
      <v-icon @click="closeModal()" color="red">mdi-close</v-icon>
    </v-toolbar>
    <v-alert v-if="errorData" tile dense type="error" v-html="'Completa los campos de Información'" />
    <v-tabs
      v-model="tab"
      centered
      icons-and-text
      dense
      @change="anonimoText(tab)"
    >
      <v-tabs-slider />
      <v-tab href="#tab-1">
        Remitente
        <v-icon>mdi-account</v-icon>
      </v-tab>

      <v-tab href="#tab-2">
        Información
        <v-icon>mdi-information-outline</v-icon>
      </v-tab>

      <v-tab href="#tab-3">
        Soportes
        <v-icon>mdi-paperclip</v-icon>
      </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item value="tab-1">
        <contact-information @savePqr="savePqr" @closeModal="closeModal" :objDataPqrs="objDataPqrs" />
      </v-tab-item>
      <v-tab-item value="tab-2">
        <formulario-pqrs
          @savePqr="savePqr"
          @closeModal="closeModal"
          :objDataPqrs="objDataPqrs"
        />
      </v-tab-item>
      <v-tab-item value="tab-3">
        <soportes @savePqr="savePqr" @closeModal="closeModal" :objDataPqrs="objDataPqrs" />
      </v-tab-item>
    </v-tabs-items>
  </v-card>
</template>

<script>
import { mapState } from "vuex";
import ContactInformation from './ContactInformation.vue'
import FormularioPqrs from './FormularioPqrs.vue'
import Soportes from './Soportes.vue'
import pqrsService from '../../services/PqrsService'

export default {
  name: "NuevaPqrs",
  components: { ContactInformation, FormularioPqrs, Soportes },
  data: () => ({
    tab: null,
    errorData: false,
    ruta_oficio: null,
    filter_estado: null,
    objDataPqrs: {
      txtEmail: '',
      txtDetalle: '',
      anonimo: false,
      dateRadicado: new Date().toISOString().substr(0, 10),
    }
  }),
  computed: {
    ...mapState(["userFirebase"])
  },
  watch: {
    userFirebase(val) {
      if(val !== null) this.objDataPqrs.txtEmail = this.userFirebase.email
    }
  },
  created() {
    this.objDataPqrs.txtEmail = this.userFirebase.email
  },
  methods: {
    anonimoText(tab) {
      if(tab === 'tab-2' || tab === 'tab-3') {
        if(!this.objDataPqrs.txtNombre || !this.objDataPqrs.txtDirection || !this.objDataPqrs.txtPhone) this.objDataPqrs.anonimo = true
      }
    },
    async getAnonimas() {
			try {
				this.loading = true
				const { data } = await pqrsService.getPqrsAnonimas()
				this.reclamos = data.pqrs_reclamos
			} catch (error) {
				(error)
			} finally {
				this.loading = false
			}
		},
    closeModal() {
      this.$emit('close-modal')
      this.objDataPqrs = {
        anonimo: false,
        txtDetalle: '',
        txtEmail: this.userFirebase.email,
        dateRadicado: new Date().toISOString().substr(0, 10)
      }
    },
    validateInfoPqrs() {
      if(
        !this.objDataPqrs.dateRadicado,
        !this.objDataPqrs.txtMunicipio,
        !this.objDataPqrs.txtTipoComunica,
        !this.objDataPqrs.txtAsunto
      ) {
        this.errorData = true;
      }
    },
    async savePqr () {
      this.validateInfoPqrs()
      if (this.errorData) return;
      if(this.objDataPqrs.anonimo === false) this.objDataPqrs.anonimo = 0
      else this.objDataPqrs.anonimo = 1
      const { data } = await pqrsService.savePqrExterno(this.objDataPqrs)
      if (data.respuesta === true) {
        this.getAnonimas()
        let id_pqr = data.id
        let consecut = data.consecutivo
        if (this.objDataPqrs.txtFiles && this.objDataPqrs.txtFiles.length > 0) {
          for (let i = 0; i < this.objDataPqrs.txtFiles.length; i++) {
            const result = await pqrsService.uploadFile(this.objDataPqrs.txtFiles[i])
            if (result.status === 200) {
              const { data } = await pqrsService.saveAdjunto({ nombre: result.data.filename, ruta: result.data.url, oficio: 'N' })
              if (data.respuesta !== true) {
                this.$toasted.error('Error al guardar archivos adjuntos', { icon: mdi-check, duration: 3000 })
              }
            }
          }
        }
        let oficio = null
        if (!this.objDataPqrs.txtFiles && this.objDataPqrs.txtOficio) {
          const info = await pqrsService.uploadFile(this.objDataPqrs.txtOficio)
          if (info.status === 200) {
            const { data } = await pqrsService.saveAdjunto({ nombre: info.data.filename, ruta: info.data.url, oficio: 'S' })
            if (data.respuesta !== true) {
              oficio = false
              this.$toasted.error('Error al guardar archivo oficio', { duration: 3000 })
            } else {
              oficio = true
            }
          }
        }
        if (oficio === true) {
          const { data } = await pqrsService.listPqr(id_pqr)
          this.ruta_oficio = data.pqrs_reclamos[0].ruta_oficio
          this.downloadAdjunto(this.ruta_oficio)
        }
        this.$emit('getFilterPqrsExt')
        this.closeModal()
        this.$toasted.success('Registro creado correctamente', { duration: 3000 })
        this.envionotificaciones(id_pqr, consecut)
      }
    },
    async envionotificaciones (id, consecut) {
      try {
        const { data } = await pqrsService.getUsersAdminPqrs()
        let datos = data
        let result = datos.map(item => {
          return item.perscod
        })
        let values = { mensajenotificacion: 'Se creó un nuevo registro en la plataforma de PQRSDF, ID ' + id,
          asuntocorreo: 'Notificación de creación de registro',
          mensajecorreo: 'Se creó un nuevo registro en la plataforma de PQRSDF, ID ' + id }
        for (let index = 0; index < result.length; index++) {
          this.$root.$emit('enviarNotificaciones', { codigopersona: result[index],
            mensajenotificacion: values.mensajenotificacion,
            asuntocorreo: values.asuntocorreo,
            mensajecorreo: values.mensajecorreo,
            codigoopcion: 57,
            numerointerno: consecut })
        }
      } catch (error) {
        (error)
      }
    },
    downloadAdjunto (url) {
      const win = window.open(url, '_blank')
      win.focus()
    }
  }
}
</script>