<template>
  <v-row class="pa-8" dense>
    <v-form ref="form">
      <v-col cols="12">
        <v-menu
          v-model="fRadicado"
          :close-on-content-click="false"
          :nudge-right="40"
          transition="scale-transition"
          offset-y
          min-width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="objDataPqrs.dateRadicado"
              label="Fecha radicado"
              prepend-icon="mdi-calendar-blank"
              readonly
              v-bind="attrs"
              v-on="on"
              :rules="[rules.required]"
            />
          </template>
          <v-date-picker
            v-model="objDataPqrs.dateRadicado"
            @input="fRadicado = false"
          />
        </v-menu>
      </v-col>
      <v-col class="d-flex" cols="12">
        <v-autocomplete
          :items="municipios"
          label="Municipio"
          item-value="idmunicipio"
          item-text="municipio"
          v-model="objDataPqrs.txtMunicipio"
          :rules="[rules.required]"
        />
        <v-autocomplete
          class="px-5"
          :items="comunicacion"
          label="Tipo de comunicación"
          item-value="id"
          item-text="descripcion"
          v-model="objDataPqrs.txtTipoComunica"
          :rules="[rules.required]"
          return-object
          @change="selecTipoComunicación()"
        />
        <v-autocomplete
          :items="medios"
          label="Medio solicitante"
          v-model="objDataPqrs.txtMedioSolicita"
          :rules="[rules.required]"
        />
      </v-col>
      <v-col cols="12">
        <v-text-field
          outlined
          v-model="objDataPqrs.txtAsunto"
          label="Asunto"
          auto-grow
          filled
          :rules="[rules.required]"
        />
      </v-col>
      <v-col cols="12">
        <v-textarea
          outlined
          v-model="objDataPqrs.txtDetalle"
          label="Descripción"
          auto-grow
          filled
        />
      </v-col>
      <v-col cols="12">
        <v-file-input
          small-chips
          label="Oficio"
          v-model="objDataPqrs.txtOficio"
          counter
          show-size
        />
      </v-col>
      <v-col cols="12" class="text-end">
        <v-btn class="pa-1" @click="savePqr()" color="success" small text v-html="'Guardar'" />
        <v-btn class="pa-1" @click="$emit('closeModal')" color="error" small text v-html="'cerrar'" />
      </v-col>
    </v-form>
  </v-row>
</template>

<script>
import pqrsService from '../../services/PqrsService'

export default {
  name: "FormularioPqrs",
  data: () => ({
    fRadicado: false,
    municipios: [],
    comunicacion: [],
    medios: ['Comunicación escrita - Carta','Comunicación escrita - Correo electrónico','Comunicación escrita - Memorando','Comunicación escrita - Página web','Comunicación escrita - Redes sociales','Comunicación verbal - Llamada telefónica','No disponible'],
    rules: {
      required: value => !!value || 'Campo requerido.',
      max: v => v.length < 5 || 'Máximo 4 caracteres'
    },
  }),
  props:{ objDataPqrs: Object },
  methods: {
    savePqr() {
      if (this.$refs.form.validate()) {
        this.$emit('savePqr')
      }
    },
    async getMunicipios() {
      const { data } = await pqrsService.getMunicipios()
      this.municipios = data.municipios
      this.objDataPqrs.txtMedioSolicita = 'Comunicación escrita - Página web'
    },
    async getTipoComunicacion() {
      const { data } = await pqrsService.getTipoComunicacion()
      this.comunicacion = data.pqr_tipo_comunicacions
    },
    selecTipoComunicación(item) {
      console.log(this.objDataPqrs.txtTipoComunica)
    }
  },
  mounted() {
    this.getTipoComunicacion()
    this.getMunicipios()
  }
}
</script>
