<template>
    <div>
    <v-data-table
      :headers="headers"
      :items="MyDataComputed"
      class="elevation-1 headers-text"
      :loading="loading"
      :search="filtro"
      :sort-by="['fecha_creacion']"
      :sort-desc="[true]"
    > 
      <template v-for="header in headers" v-slot:[`header.${header.value}`]="{ header }">
        <span v-html="header.text" />
      </template>
      <template v-slot:item.acciones="{ item }">
        <v-icon
          @click="getPdf(item)"
          color="red"
          :disabled="item.id_estado === 4?false:true"
        >
          mdi-file-pdf-box
        </v-icon>
      </template>
      <template v-slot:item.estado="{ item }">
        <v-chip
          :color="getColor(item.estado)"
          dark
        >
          {{ item.estado }}
        </v-chip>
      </template>
      <template v-slot:top>
        <v-toolbar flat>
          <v-dialog
            v-model="newPqrs"
            max-width="500px"
            persistent
          >
            <template v-slot:activator="{ on, attrs }">
                <v-btn
                  small
                  color="primary"
                  dark
                  v-bind="attrs"
                  v-on="on"
                  v-html="'Nueva PQRS'"
                />
                <v-row align="center">
                  <v-col cols="12">
                    <v-alert v-if="aletrDatesFiler" type="error" dense v-html="mensajeAlerta" />
                  </v-col>
                  <v-col cols="4" align="center" justify="center" class="pt-0 pb-5">
                    <v-tooltip top>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" class="mr-1" color="purple" fab x-small dark @click="getFilterPqrsExt(0)">
                          <v-icon>mdi-account-plus</v-icon>
                        </v-btn>
                      </template>
                      <span>Abiertas</span>
                    </v-tooltip>
                    <v-tooltip top>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" class="mr-1" color="green" fab x-small dark @click="getFilterPqrsExt(1)">
                          <v-icon>mdi-account-check</v-icon>
                        </v-btn>
                      </template>
                      <span>Asignadas</span>
                    </v-tooltip>
                    <v-tooltip top>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" class="mr-1" color="amber" fab x-small dark @click="getFilterPqrsExt(2)">
                          <v-icon>mdi-briefcase</v-icon>
                        </v-btn>
                      </template>
                      <span>En desarrollo</span>
                    </v-tooltip>
                    <v-tooltip top>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" class="mr-1" color="red" fab x-small dark @click="getFilterPqrsExt(4)">
                          <v-icon>mdi-close-circle</v-icon>
                        </v-btn>
                      </template>
                      <span>Cerradas</span>
                    </v-tooltip>
                    <v-tooltip top>
                      <template v-slot:activator="{ on }">
                        <v-btn v-on="on" class="mr-1" color="black" fab x-small dark @click="getFilterPqrsExt(null)">
                          <v-icon>mdi-minus-circle</v-icon>
                        </v-btn>
                      </template>
                      <span>Sin filtros</span>
                    </v-tooltip>
                  </v-col>
                  <v-col cols="2">
                    <v-dialog
                      ref="dialog"
                      v-model="modalDesde"
                      :return-value.sync="dateDesde"
                      persistent
                      width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="dateDesde"
                          label="Desde"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        />
                      </template>
                      <v-date-picker
                        v-model="dateDesde"
                        scrollable
                      >
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          color="primary"
                          @click="modalDesde = false"
                        >
                          Cancel
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="$refs.dialog.save(dateDesde)"
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-dialog>
                  </v-col>
                  <v-col cols="2">
                    <v-dialog
                      ref="dialog1"
                      v-model="modalHasta"
                      :return-value.sync="dateHasta"
                      persistent
                      width="290px"
                    >
                      <template v-slot:activator="{ on, attrs }">
                        <v-text-field
                          v-model="dateHasta"
                          label="Hasta"
                          prepend-icon="mdi-calendar"
                          readonly
                          v-bind="attrs"
                          v-on="on"
                        ></v-text-field>
                      </template>
                      <v-date-picker
                        v-model="dateHasta"
                        scrollable
                      >
                        <v-spacer></v-spacer>
                        <v-btn
                          text
                          color="primary"
                          @click="modalHasta = false"
                        >
                          Cancel
                        </v-btn>
                        <v-btn
                          text
                          color="primary"
                          @click="getFilterDate(),$refs.dialog1.save(dateHasta)"
                        >
                          OK
                        </v-btn>
                      </v-date-picker>
                    </v-dialog>
                  </v-col>
                  <v-col cols="4">
                    <v-text-field
                      label="Filtrar"
                      outlined
                      dense
                      hide-details
                      prepend-icon="mdi-filter-outline"
                      v-model="filtro"
                    />
                  </v-col>
              </v-row>
            </template>
            <nueva-pqrs
              ref="enviofiltro"
              @close-modal="closeModalNewPqrs"
              @getFilterPqrsExt="getInfoDataPqrs"
            />
          </v-dialog>
        </v-toolbar>
      </template>
    </v-data-table>
    <v-btn
      v-if="alertPdef"
      color="pink"
      dark
      absolute
      top
      right
    >
      <v-progress-circular
        size="20"
        :width="3"
        indeterminate
      />
      <span class="ml-1 text-caption" v-html="'En un momento su documento se abrirá...'" />
    </v-btn>
    </div>
</template>

<script>
import { mapState, mapMutations, mapActions } from "vuex";
import configService from '../../services/config'
import pqrsService from '../../services/PqrsService'
import NuevaPqrs from './NuevaPqrs.vue'

export default {
  name: "TableRegister",
  data: () => ({
      mensajeAlerta: '',
      modalDesde: false,
      modalHasta: false,
      aletrDatesFiler: false,
      dateDesde: '',
      dateHasta: '',
      alertPdef: false,
      newPqrs: false,
      dialogDelete: false,
      loading: false,
      filtro: null,
      reclamos: [],
      headers: [
        { text: 'Radicado', align: 'center', value: 'consecutivo' },
        { text: 'Fecha y hora creación', align: 'left', value: 'fecha_creacion' },
        { text: 'Estado', align: 'center', value: 'estado' },
        { text: 'Tipo de comunicación', align: 'center', value: 'descripcion' },
        { text: 'Acciones', align: 'center', value: 'acciones' }
      ]
  }),
  components: { NuevaPqrs },
  computed: {
    ...mapState(["userFirebase"]),
      formTitle () {
        return this.editedIndex === -1 ? 'New Item' : 'Edit Item'
      },
      MyDataComputed () {
      let resultado = this.reclamos
      resultado.map(item => {
        let calculo
        let a = this.moment()
        let b = this.moment(item.fecha_creacion)
        let resta = a.diff(b, 'days')
        calculo = parseInt(item.dias_respuesta) - parseInt(resta)
        item.dias_respuesta = calculo < 0 ? 0 : calculo
      })
      return resultado
    }
  },
  watch: {
      dialog (val) {
        val || this.close()
      },
      dialogDelete (val) {
        val || this.closeDelete()
      },
      userFirebase(val) {
        if(val) this.getInfoDataPqrs()
      }
  },
  methods: {
    ...mapActions(["UserFirebaseAction"]),
    ...mapMutations(["SetUserFirebase"]),
    async getPdf(item) {
      this.alertPdef = true
      await pqrsService.previewpdfPqrsdf(item.id)
      window.open(configService.apiUrl + '/previewpdfPqrsdf?id=' + item.id, '_blank')
      this.alertPdef = false
    },
    getColor (estado) {
        switch (estado) {
          case 'Abierta':
            return 'purple'
            break
          case 'Asignada':
            return 'green'
            break
          case 'En desarrollo':
            return 'amber'
            break
          case 'Cerrado':
            return 'red'
            break
        }
    },
    closeModalNewPqrs() {
      this.newPqrs = false
    },
    cerrarsesionAdmin () {
      this.$session.destroy()
      this.SetUserFirebase(null)
      this.$router.push('/autenticacion')
    },
    async getFilterDate() {
      if(this.dateDesde !== '' && this.dateHasta !== '') {
        let desde = this.dateDesde
        let hasta = this.dateHasta
        if(desde < hasta) {
          const { data } = await pqrsService.getFilterDateExt(desde,hasta,this.userFirebase.email)
          if(data.respuesta === true && data.datos.length > 0) {
            this.reclamos = data.datos
          }
          else {
            this.reclamos = []
            this.aletrDatesFiler = true
            this.mensajeAlerta = 'No se encontraron datos en este rango de fechas...'
            setTimeout(() => {
              this.aletrDatesFiler = false
              this.getInfoDataPqrs()
            }, 3000)
          }
        }else {
          this.aletrDatesFiler = true
          this.mensajeAlerta = 'Fechas no admitidas'
          setTimeout(() => {
            this.aletrDatesFiler = false
          }, 3000)
        }
      }else {
        this.aletrDatesFiler = true
        setTimeout(() => {
          this.aletrDatesFiler = false
        }, 3000)
      }
    },
    async getInfoDataPqrs () {
      try {
        this.loading = true
        const { data } = await pqrsService.getPqrsExternas(this.userFirebase.email)
        this.reclamos = data.pqrs_reclamos
      } catch (error) {
        (error)
      } finally {
        this.loading = false
      }
    },
    async getFilterPqrsExt(filter) {
      if(filter === null) this.getInfoDataPqrs()
      else {
        const { data } = await pqrsService.getFilterPqrsExt(filter,this.userFirebase.email)
        this.reclamos = data.pqrs_reclamos
      }
    }
  },
  mounted() {
    this.UserFirebaseAction()
    this.getInfoDataPqrs()
  }
}
</script>

<style scoped>
span {
  margin-top: 2px;
  font-size: medium;
}
</style>


