<template>
  <v-row class="pa-8" dense>
    <v-col>
      <v-form ref="form">
        <v-text-field
          v-model="objDataPqrs.txtNombre"
          label="Nombre completo"
          name="name"
          type="text"
          dense
          prepend-icon="mdi-account"
          v-on:keypress="isLetter($event)"
          :rules="objDataPqrs.anonimo === true?[]:[rules.required, rules.letras]"
          :disabled="objDataPqrs.anonimo === true?true:false"
        />
        <v-text-field
          class="mt-5"
          v-model="objDataPqrs.txtId"
          label="Número de Identificación"
          name="id"
          type="text"
          dense
          prepend-icon="mdi-badge-account-horizontal"
          :rules="objDataPqrs.anonimo === true?[]:[rules.required]"
          :disabled="objDataPqrs.anonimo === true?true:false"
        />
        <v-text-field
          class="mt-5"
          v-model="objDataPqrs.txtDirection"
          label="Dirección"
          name="id"
          type="text"
          dense
          prepend-icon="mdi-map-marker"
          :rules="objDataPqrs.anonimo === true?[]:[rules.required]"
          :disabled="objDataPqrs.anonimo === true?true:false"
        />
        <v-text-field
          class="mt-5"
          v-model="objDataPqrs.txtPhone"
          label="Número de teléfono"
          name="phone"
          type="number"
          dense
          prepend-icon="mdi-phone"
          :rules="objDataPqrs.anonimo === true?[]:[rules.required]"
          :disabled="objDataPqrs.anonimo === true?true:false"
        />
        <v-text-field
          class="mt-5"
          v-model="objDataPqrs.txtEmail"
          label="Correo electrónico"
          name="email"
          type="text"
          dense
          disabled
          prepend-icon="mdi-email"
        />
        <v-checkbox @click="cleanData()" label="Perfil Anónimo" v-model="objDataPqrs.anonimo" />
      </v-form>
    </v-col>
    <v-col cols="12" class="text-end">
      <v-btn class="pa-1" @click="savePqr()" color="success" small text v-html="'Guardar'" />
      <v-btn class="pa-1" @click="$emit('closeModal')" color="error" small text v-html="'cerrar'" />
    </v-col>
  </v-row>
</template>

<script>
export default {
  name: "ContactInformation",
  data: () => ({
    rules: {
      required: value => !!value || 'Campo requerido.',
      max: v => v.length < 5 || 'Máximo 4 caracteres',
      letras: value => {
        const letra = /(^[a-zA-ZÀ-ÿ\u00f1\u00d1]+)([\s]+)([^\s]+[a-zA-ZÀ-ÿ\u00f1\u00d1 ]+)$/;
        return letra.test(value) || "Ingresa mínimo nombre y apellido.";
      }
    }
  }),
  props:{ objDataPqrs: Object },
  methods: {
    isLetter(e) {
      let char = String.fromCharCode(e.keyCode);
      if(/^[A-Za-z ]+$/.test(char)) return true;
      else e.preventDefault();
    },
    savePqr() {
      if (this.$refs.form.validate()) {
        this.$emit('savePqr')
      }
    },
    cleanData() {
      if(this.objDataPqrs.anonimo) {
        this.objDataPqrs.txtNombre = null
        this.objDataPqrs.txtId = null
        this.objDataPqrs.txtDirection = null
        this.objDataPqrs.txtPhone = null
      }
    }
  }
}
</script>